.search-bar-container {
    width: 100%;
    display: flex;
    margin: 4.3rem 0 1rem 0.25rem;
    align-items: center;
    justify-content: center;
    position: relative; /* Make the container relative */
}

.search-bar-form {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.search-results-dropdown {
    position: absolute;
    top: 100%; /* Position it right below the search input */
    left: 0;
    width: 100%;
    max-height: 300px;
    overflow-y: auto;
    background-color: rgba(0, 0, 0, 0.85);
    backdrop-filter: blur(10px);
    border-radius: 5px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
    scrollbar-color: rgba(255, 219, 149, 0.2) transparent;
    z-index: 1000;
}

.search-result-item {
    display: flex;
    align-items: center;
    padding: 10px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
    width: 100%;
}

.search-result-item:hover {
    background-color: rgba(255, 255, 255, 0.1);
}

.search-result-image {
    width: 100px;
    height: 150px;
    object-fit: cover;
    border-radius: 5px;
    margin-right: 10px;
}

.search-result-info {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.search-result-info h4 {
    font-size: 16px;
    color: #fff;
    margin: 0;
}

.search-result-actions {
    display: flex;
    gap: 10px;
    margin-top: 5px;
}

.border{
    max-height: 70px;
    height: 100%;
    width: 50%;
    position: absolute;
    overflow: hidden;
    z-index: -1;
    /* Border Radius */
    border-radius: 12px;
    filter: blur(3px);
}
.input {
    background-color: #010201;
    border: none;
    /* padding:7px; */
    width: 100%;
    height: 56px;
    border-radius: 10px;
    color: white;
    padding-inline: 59px;
    font-size: 18px;
}

.input::placeholder {
    color: #c0b9c0;
}

.input:focus {
    outline: none;
}

#main:focus-within > #input-mask {
    display: none;
}

#input-mask {
    pointer-events: none;
    width: 100%;
    height: 20px;
    position: absolute;
    background: linear-gradient(90deg, transparent, black);
    top: 18px;
    left: 70px;
}
#pink-mask {
    pointer-events: none;
    width: 99%;
    height: 20px;
    position: absolute;
    background: #cf30aa;
    top: 10px;
    left: 5px;
    filter: blur(20px);
    opacity: 0.8;
    animation:leftright 10s ease-in infinite;
    transition: all 2s;
}
#main:hover > #pink-mask {
 animation: rotate 4s linear infinite;
    opacity: 0;
}

.border {
    max-height: 59px;
    border-radius: 11px;
    filter: blur(0.5px);
}
.border::before {
    content: "";
    z-index: -2;
    text-align: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(70deg);
    position: absolute;
    width: 100%;
    height: 600px;
    filter: brightness(1.3);
    background-repeat: no-repeat;
    background-position: 0 0;
    background-image: conic-gradient(
            #1c191c,
            #402fb5 5%,
            #1c191c 14%,
            #1c191c 50%,
            #cf30aa 60%,
            #1c191c 64%
    );
 animation: rotate 4s 0.1s linear infinite;
    transition: all 2s;
}

#poda:hover > .glow::before {
    transform: translate(-50%, -50%) rotate(240deg);
}


#poda:hover > .border::before {
    transform: translate(-50%, -50%) rotate(250deg);
}

#poda:hover > .glow::before {
    transform: translate(-50%, -50%) rotate(-120deg);
}


#poda:hover > .border::before {
    transform: translate(-50%, -50%) rotate(-110deg);
}


#poda:focus-within > .glow::before {
    transform: translate(-50%, -50%) rotate(420deg);
    transition: all 4s;
}
#poda:focus-within > .border::before {
    transform: translate(-50%, -50%) rotate(430deg);
    transition: all 4s;
}

.glow {
    overflow: hidden;
    filter: blur(30px);
    opacity: 0.4;
    max-height: 130px;
}
.glow:before {
    content: "";
    z-index: -2;
    text-align: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(60deg);
    position: absolute;
    width: 100%;
    height: 999px;
    background-repeat: no-repeat;
    background-position: 0 0;
    /*border color, change middle color*/
    background-image: conic-gradient(
            #000,
            #402fb5 5%,
            #000 38%,
            #000 50%,
            #cf30aa 60%,
            #000 87%
    );
    /* change speed here */
 animation: rotate 4s 0.3s linear infinite;
    transition: all 2s;
}

@keyframes rotate {
    100% {
        transform: translate(-50%, -50%) rotate(450deg);
    }
}
@keyframes leftright {
    0% {
        transform: translate(0px, 0px);
        opacity: 1;
    }

    49% {
        transform: translate(250px, 0px);
        opacity: 0;
    }
    80% {
        transform: translate(-40px, 0px);
        opacity: 0;
    }

    100% {
        transform: translate(0px, 0px);
        opacity: 1;
    }
}

#filter-icon {
    position: absolute;
    top: 8px;
    right: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 2;
    max-height: 40px;
    max-width: 38px;
    height: 100%;
    width: 100%;

    isolation: isolate;
    overflow: hidden;
    /* Border Radius */
    border-radius: 10px;
    background: linear-gradient(180deg, #161329, black, #1d1b4b);
    border: 1px solid transparent;
}
.filterBorder {
    height: 42px;
    width: 40px;
    position: absolute;
    overflow: hidden;
    top: 7px;
    right: 7px;
    border-radius: 10px;
}

.filterBorder::before {
    content: "";

    text-align: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(90deg);
    position: absolute;
    width: 600px;
    height: 600px;
    background-repeat: no-repeat;
    background-position: 0 0;
    filter: brightness(1.35);
    background-image: conic-gradient(
            rgba(0, 0, 0, 0),
            #3d3a4f,
            rgba(0, 0, 0, 0) 50%,
            rgba(0, 0, 0, 0) 50%,
            #3d3a4f,
            rgba(0, 0, 0, 0) 100%
    );
    animation: rotate 4s linear infinite;
}
#main {
    position: relative;
    width: 100%;
}
#search-icon {
    position: absolute;
    left: 20px;
    top: 15px;
}
